.wrapper {
  width: 100%;
  box-sizing: border-box;
  max-width: 1500px;
  margin: 0 auto;
  padding: 0 25px;
}

@media (max-width: 499.98px) {
  .wrapper {
    padding: 0 15px;
  }
}

.wrapper--full {
  max-width: none !important;
}

.wrapper--narrow {
  max-width: 1000px !important;
}

.wrapper--extra-narrow {
  max-width: 500px !important;
}

